.sign-in-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #ebebeb;
  }
  
  .sign-in-container h1 {
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .sign-in-container form {
    display: flex;
    flex-direction: column;
  }
  
  .sign-in-container input[type="email"],
  .sign-in-container input[type="password"] {
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .sign-in-container button[type="submit"] {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .sign-in-container button[type="submit"]:hover {
    background-color: #45a049;
  }
  

  


  